*{
 margin: 0;
 padding: 0;
 box-sizing: border-box; 
}

a{
  text-decoration: inherit;
  color: inherit;
}

.app{
  min-height: 95vh;
  border: 8px solid #39445a93;
  margin: 5px;
  border: 5px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Jost", sans-serif;
}


a {
    text-decoration: none;
}

li {
    list-style: none;
}
.main-nav {
    width: 100%;
    height: 6rem;
    display: grid;
    grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.logo {
    display: grid;
    /* background-color: #3b5998; */
    grid-column: 2/3;
    justify-content: start;
    align-items: center;
}

.menu-link {
    grid-column: 3/4;
}

    .menu-link ul {
        height: 6rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

.social-media {
    grid-column: 4/5;
}

    .social-media ul {
        height: 6rem;
        display: grid;
        grid-template-columns: 3fr repeat(3, 1fr);
        align-items: center;
        justify-content: flex-end;
    }

        .social-media ul li {
            text-align: right;
        }

            .social-media ul li:first-child {
                grid-column: 2/3;
            }

.logo h2 {
    font-size: 2.5rem;
    font-weight: 400;
    text-transform: uppercase;
    background: -webkit-linear-gradient(#eee, rgb(69, 57, 248));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

    .logo h2 span {
        font-size: 3.5rem;
    }

.menu-link ul li {
    font-size: 1.8rem;
}

    .menu-link ul li a {
        text-transform: capitalize;
        color: rgba(0, 0, 3, 0.8);
        transition: 0.5s;
    }

    .menu-link ul li:hover > a {
        -webkit-transform-origin: left;
                transform-origin: left;
        color: rgba(0, 0, 3, 1);
        text-decoration: underline;
        transition: 0.5s;
    }

.social-media ul li {
    font-size: 1.8rem;
}

.social-media .hamburger-menu {
    display: none;
}



.hero-section {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

    .hero-section p {
        font-size: 3rem;
        text-transform: capitalize;
    }

    .hero-section h1 {
        font-size: 5rem;
        text-transform: uppercase;
        text-align: center;
    }


@media (max-width: 1080px) {
    .main-nav {
        height: 8rem;
        grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
    }

    .logo,
    .menu-link ul,
    .social-media ul {
        height: 8rem;
    }
}

@media (max-width: 998px) {
    .main-nav {
        height: 7rem;
        grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
    }

    .menu-link {
        display: none;
    }

    .logo,
    .social-media ul {
        height: 7rem;
    }

    .mobile-menu-link {
        grid-column: 2/4;
        position: relative;
        z-index: 99;
    }

    .mobile-menu-link {
        background-color: white;
        height: 20rem;
        display: grid;
        grid-column: 2/5;
        align-items: center;
        padding-left: 3rem;
        transition: all 2s linear;
        -webkit-transform-origin: top;
                transform-origin: top;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    }

        .mobile-menu-link ul {
            height: 20rem;
            display: flex;
            justify-content: space-around;
            flex-direction: column;
            align-items: start;
        }

            .mobile-menu-link ul li:first-child {
                transition-delay: 0.2s;
            }

    .social-media {
        grid-row: 1/2;
        grid-column: 3/5;
        justify-items: end;
        align-items: center;
        transition: all 2s linear;
    }

        .social-media .social-media-desktop {
            height: 0;
            display: none;
        }

    .social-media {
        height: 7rem;
        display: flex;
        justify-self: end;
        align-items: center;
    }

        .social-media .hamburger-menu {
            display: block;
            font-size: 2.5rem;
        }
}

@media (max-width: 798px) {
    .main-nav {
        height: 6rem;
        grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
    }

    .logo,
    .social-media ul {
        height: 6rem;
    }

    .social-media {
        height: 6rem;
        display: flex;
        justify-self: end;
        align-items: center;
    }

        .social-media .hamburger-menu {
            display: block;
            font-size: 2.5rem;
        }
}

@media (max-width: 520px) {
    .main-nav {
        height: 6rem;
        grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
    }

    .logo,
    .social-media ul {
        height: 6rem;
    }

        .logo h2 {
            font-size: 2rem;
        }

    .social-media {
        height: 6rem;
        display: flex;
        justify-self: end;
        align-items: center;
    }

        .social-media .hamburger-menu {
            display: block;
            font-size: 2.5rem;
        }

    /* hero section  */

    .hero-section h1 {
        font-size: 3.8rem;
    }
}
.home-content {
    display: flex;
    justify-content: space-around;
  }
  
  .home-settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
  }
  
  .banner {
    width: 55%;
    align-self: center;
    padding: 8px;
  }
  
  @media (max-width: 800px) {
    .home-content {
      flex-direction: column-reverse;
      align-items: center;
    }
    .banner {
      width: 85%;
      padding: 0;
      padding-top: 20px;
    }
    .home-settings {
      width: 100%;
    }
  }
  
  .settings__select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8 1;
    justify-content: space-evenly;
  }

.error {
    width: 100%;
    padding: 10px; 
    margin-bottom: 10px;
    border-radius: 4px;
    background-color: #ff006e;
    text-align: center;
    color: white;
    text-transform: capitalize ;
}

.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
}

